import React from "react";
import { useState } from "react";
import "./styles/mainInfo.css";
import HelpIcon from "@mui/icons-material/Help";
import CloseIcon from '@mui/icons-material/Close';
import Button from 'react-bootstrap/Button';


function MainInfoButton() {

  const [enabled, setEnabled] = useState(false);

  const handleClick = () => {
    setEnabled(!enabled);
  };

  return (
    <>
      <div id="mainInfoButtonContainer" className={enabled ? 'disabled' : ''} onClick={handleClick}>
        <HelpIcon id="mainInfoButton" />
      </div>
      <MainInfoBox active={enabled} exitHandler={handleClick} />
    </>
  );
}

const MainInfoBox = (props) => {

  if (!props.active) return null;


  return (
    <div id="mainInfoBG">
      <div id="mainInfoContainer">
        <ExitMainInfoButton exitHandler={props.exitHandler} />
        <Instrucciones />
      </div>
    </div>
  );
};

function ExitMainInfoButton(props)
{
  return (
    <>
    <Button variant="outline-light" id="exitMainInfoButton" onClick={props.exitHandler}>{<CloseIcon />}</Button>{' '}
    
    {/* <div id="exitMainInfoButton" onClick={props.exitHandler}>
      <CloseIcon />
    </div> */}
    </>
  );
}

function Instrucciones() {
  return (
    <>
      <div className="infoBox">
        <h1>Instrucciones de uso de PokRoll</h1>
        <h2>Bienvenido a PokRoll!</h2>
        <p>PokRoll es un juego en lnea de mquinas tragaperras <b>sin ningn gasto de dinero real</b> de Pokmon, en el que podrs competir contra
          otros jugadores para encontrar al Pokmon ms raro posible
        </p>
        <p>En PokRoll, ofrecemos los siguientes servicios para todos los jugadores:</p>
        <ul>
          <li><b>RULETA</b>: Con la ruleta, podrs conseguir TODOS los Pokmon que t quieras! Adems, tienes distintas ruletas para elegir,
            cada una de ellas tiene una mayor probabilidad para obtener mejores Pokmon.
            <ul><br/>
              <li><b>TIER 1</b>: En esta ruleta, encontrars mayormente Pokmon comunes, como Pidgey o Rattata.</li>
              <li><b>TIER 2</b>: En esta ruleta, encontrars Pokmon infrecuentes, como Chespin o Eevee.</li>
              <li><b>TIER 3</b>: En esta ruleta, predominan Pokmon peculiares, como Quilava o Croconaw.</li>
              <li><b>TIER 4</b>: En esta ruleta, aparecern Pokmon picos, como Charizard o Garchomp.</li>
              <li><b>TIER 5</b>: En esta ruleta, encontrars a los Pokmon legendarios, como Mewtwo o Giratina.
                <ul>
                  <li>...Y con un poco de suerte, Podrs conseguir Pokmon singulares, como Darkrai o Genesect!</li>
                </ul>
              </li>
            </ul><br/>
            A lo largo del tiempo, se han encontrado en las tiradas especies de Pokmon muy raras... Se han encontrado Pokmon variocolores, los cuales tienen 
            un color muy diferente al Pokmon original; especies raras de Pokmon que no se suelen ver, e incluso Pokmon bajo el poder de la Megaevolucin! <br/>

            Cuando hagas alguna tirada, tendrs cierta posibilidad de encontrarte con alguno de estos Pokmon... Y habr confeti para celebrarlo! Coleccionalos a todos!<br/><br/>

            Pese a que por cada Tier de ruleta se obtienen Pokmon ms raros, ten en cuenta que cada uno cuesta ms que el anterior!<br/><br/>

            (Para cambiar el Tier de la ruleta, pulsa sobre los botones ubicados a la izquierda del botn TIRAR, donde adicionalmente podrs ver qu TIER de ruleta has seleccionado)
          

          </li><br/>
          <li><b>POKDEX</b>: Con la Pokdex integrada en PokRoll, podrs consultar todos los Pokmon que has obtenido a travs de todas las tiradas
            que hayas realizado. Ten en cuenta que, incluso liberando a tus Pokmon, el registro no se perder!<br/><br/>

            Por cierto, puede que si encuentras a todos los Pokmon de la Pokdex, encuentres con mayor facilidad a los anteriormente mencionados Pokmon variocolor...
          </li><br/>
          <li><b>ALMACN</b>: En el almacn, se guardarn todos los Pokmon que hayas obtenido. Adicionalmente, podrs consultar los datos de estos Pokmon,
            como pueden ser sus estadsticas, tipos, fecha de obtencin, rareza y ms.<br/><br/>
            
            Tambin tienes una opcin de liberar a tus Pokmon a cambio de
            puntos, con los cuales podrs realizar ms tiradas en la Ruleta para conseguir ms Pokmon.<br/><br/>

            En el almacn, adicionalmente dispondrs de una lista de ordenaciones y de filtros que podrs usar a tu antojo, y los cuales se aplicarn cuando se cambie alguno de los valores.
          </li><br/>
          <li><b>MARCADORES</b>: En esta ventana, podrs ver distintas clasificaciones dentro de la aplicacin PokRoll entre todos los usuarios, en el que podrs
            consultar quines son los jugadores que tienen mejores Pokmon, o los ms raros, entre otras ms opciones.
          </li><br/>
          <li><b>INTERCAMBIO</b>: Con esta funcin, podrs conectar con otro jugador para poder intercambiar vuestros Pokmon.</li>
        </ul>
      </div>
    </>
  );
}


export default MainInfoButton;
