import axios from 'axios';
import React from "react";

/**
 * Librera para obtener datos de la PokeAPI y deserializarlos.
 * til para obtener los tipos de un pokmon o su sprite imagen.
 */

/**
 * Devuelve los datos (JSON) del pokmon
 * @param dexNum [Integer | Obligatorio] DexNum del pokmon
 */
export const GetDataByDexNum = async (dexNum) =>
{
    try 
    {
        const response = await axios.get(`https://pokeapi.co/api/v2/pokemon/${dexNum}`);
        return response.data;
    } 
    catch(error) 
    {
        // console.error("Error al conseguir informacin del pokmon " + error);
        return null;
    }
}

/**
 * Devuelve los datos (JSON) del pokmon
 * @param pokemonName [String | Obligatorio] Nombre del pokmon
 */
export const GetDataByName = async (pokemonName) =>
{   
    try 
    {
        const response = await axios.get(`https://pokeapi.co/api/v2/pokemon/${pokemonName}`);
        return response.data;
    }
    catch(error)
    {
        // console.error("Error al conseguir informacin del pokmon " + error);
        return null;
    }
}

/**
 * Devuelve el nombre del pokmon
 * @param data [JSON | Obligatorio] Datos del pokmon
 */
export const GetName = (data) =>
{
    if(data !== undefined && data !== null)
    {
        return data.species.name;   
    }
    return null;
}

/**
 * Devuelve el dexNum del pokmon
 * @param data [JSON | Obligatorio] Datos del pokmon
 */
export const GetDexNum = (data) =>
{
    if(data !== undefined && data !== null)
    {
        return data.id;   
    }
    return null;
}

/**
 * Devuelve el sprite del pokmon en objeto <img/>
 * @param data [JSON | Obligatorio] Datos del pokmon
 * @param isShiny [Boolean] True si es shiny
 */
export function GetImage(data, isShiny)
{
    const name = GetName(data);
    let image = (<></>);
    if(name !== null)        
    {
        let shiny = "normal";
        if(isShiny === true)
        {
            shiny = "shiny";
        }

        const url = "https://img.pokemondb.net/sprites/home/" + shiny + "/" + name + ".png";

        image = (<img className={"pokemonImg " + shiny + "Resplandor"} aria-hidden="true" tabindex="-1" src={url} alt={name}></img>);
    }
    return (
        <>
            {image}
        </>
    );
}

/**
 * Devuelve el sprite del pokmon en objeto en caso de que tenga una variant <img/>
 * @param variant nombre de la VARIANT del pokemon
 * @param isShiny [Boolean] True si es shiny
 */
export function GetVariantImage(variant, isShiny)
{
    let image = (<></>);
    if(variant !== null)        
    {
        let shiny = "normal";
        if(isShiny === true)
        {
            shiny = "shiny";
        }

        const url = "https://img.pokemondb.net/sprites/home/" + shiny + "/" + variant + ".png";

        image = (<img className={"pokemonImg " + shiny + "Resplandor"} aria-hidden="true" tabindex="-1" src={url} alt={variant}></img>);
    }
    return (
        <>
            {image}
        </>
    );
}

/**
 * Devuelve el primer tipo del pokmon
 * @param data [JSON | Obligatorio] Datos del pokmon
 */
export const GetFirstType = (data) =>
{
    if (data !== undefined && data !== null) 
    {
        return data.types[0].type.name;
    }
    return null;
}

/**
 * Devuelve el segundo tipo del pokmon (si lo tiene, sino null)
 * @param data [JSON | Obligatorio] Datos del pokmon
 */
export const GetSecondType = (data) =>
{
    if (data !== undefined && data !== null) 
    {
        if (data.types[1] != null) 
        {
            return data.types[1].type.name;
        } 
    }
    return null;
}

/**
 * Devuelve el tipo indicado traducido al espaol y con la primera letra mayscula
 * @param name Nombre del tipo (obtenido del getNType())
 */
export const GetPrettyTypeNameSpanish = (name) => 
{
    return types[name] || null;
}

/*
* Traduccin de los tipos
*/
